/* PC端 */
.login-layout {
  width: 100%;
  height: 100%;
  background-color: #fff;
  position: relative;
  overflow: hidden;
}

.login-layout__content {
  width: 300px;
  min-height: 300px;
  padding: 25px;
  background-color: rgba(255, 255, 255, .5);
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  right: 15%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  box-shadow: 0 1px 6px rgba(0, 0, 0, .2);
  border-radius: 4px;
}
/* PC端 */
.login__title {
  font-size: 18px;
  margin-bottom: 20px;
}

.login__error {
  min-height: 32px;
}
